import { CollectionType, HttpMethod, updateCollectionType } from "api/collectionTypes";
import classNames from "classnames";
import PageTitle from "components/PageTitle";
import Select from "components/Select";
import { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { RootState } from "store";
import { CmsEvent } from "api/collectionTypes";
import Button from "components/Button";
import Input from "components/Input";
import SimpleBar from "simplebar-react";
import { useHistory } from "react-router-dom";
import EmptyScreen from "components/EmptyScreen";

export interface WebhooksProps {}

const Webhooks: React.FC<WebhooksProps> = () => {
  const [collectionTypeId, setCollectionTypeId] = useState<string>();
  const [colType, setColType] = useState<CollectionType>();
  const [prevColType, setPrevColType] = useState<CollectionType>();
  const collectionTypes = useSelector((state: RootState) =>
    state.collectionTypes.filter((x) => !["EmailTemplatesReservedCollection"].includes(x.id))
  );

  const dispatch = useDispatch();

  const events: CmsEvent[] = ["find", "find one", "create", "update", "delete"];
  const httpsMethods = ["POST", "DELETE", "PATCH", "PUT", "GET"];

  useEffect(() => {
    if (collectionTypeId) {
      const obj = collectionTypes.find((x) => x.id === collectionTypeId);
      setColType(JSON.parse(JSON.stringify(obj)));
      setPrevColType(JSON.parse(JSON.stringify(obj)));
    }

    if (collectionTypes.length && !collectionTypeId) {
      setCollectionTypeId(collectionTypes[0].id);
    }
  }, [collectionTypeId, collectionTypes]);

  const changed = JSON.stringify(prevColType) !== JSON.stringify(colType);

  const history = useHistory();

  return (
    <div>
      <PageTitle className="mb-1">Webhooks</PageTitle>
      <div className="mb-12">Create webhooks for events in each collection</div>

      {!collectionTypes.length ? (
        <EmptyScreen
          title="Create collection to add webhooks for"
          buttonTitle="Create collection type"
          onCreate={() => history.push("/collections")}
        ></EmptyScreen>
      ) : (
        <div className="flex flex-wrap lg:flex-nowrap">
          <div className="max-w-192px mb-3 mr-4 block lg:hidden w-full">
            <Select
              onChange={(e) =>
                !changed || window.confirm("Changes will be lost")
                  ? setCollectionTypeId(e.target.value)
                  : {}
              }
              value={collectionTypeId}
              options={collectionTypes.map((x) => ({ value: x.id, label: x.name }))}
            ></Select>
          </div>

          <SimpleBar
            className="min-w-192px flex-shrink-0 mr-4 hidden lg:block select-none max-h-72 pr-4 scrollbar-dark"
            autoHide={false}
          >
            {collectionTypes.map((x) => (
              <div
                onClick={() =>
                  !changed || window.confirm("Changes will be lost")
                    ? setCollectionTypeId(x.id)
                    : {}
                }
                key={x.id}
                className={`mb-1 capitalize truncate cursor-pointer ${
                  collectionTypeId === x.id ? "bg-orange-300" : "hover:bg-gray-300"
                } rounded h-34px leading-34px px-3`}
              >
                {x.name}
              </div>
            ))}
          </SimpleBar>
          <div className="lg:flex-grow w-full rounded bg-gray-300 p-7">
            {!colType ? null : (
              <>
                <div className="flex justify-between mb-4">
                  <div className="text-22px capitalize font-medium mb-3">{colType.name}</div>
                  <Button
                    disabled={!changed}
                    className={classNames("mb-3", {
                      "cursor-default bg-white text-gray-400": !changed,
                      "bg-orange-300 hover:bg-orange-301": changed,
                    })}
                    onClick={() => {
                      updateCollectionType(colType.docId, colType);
                      dispatch({
                        type: "UPDATE_COLLECTION",
                        docId: colType.docId,
                        payload: colType,
                      });
                    }}
                  >
                    {!changed ? "Saved!" : "Save"}
                  </Button>
                </div>

                {events.map((x) => (
                  <div className="flex mb-2">
                    <div className="w-32">On {x}</div>
                    <Select
                      className="mr-2"
                      white
                      options={httpsMethods.map((m) => ({ value: m, label: m }))}
                      value={colType.webhooks[x].method}
                      onChange={(e) => {
                        const webhooks = { ...colType.webhooks };
                        webhooks[x].method = e.target.value as HttpMethod;
                        setColType((prev) => (!prev ? prev : { ...prev, webhooks }));
                      }}
                    ></Select>
                    <div className="flex-grow">
                      <Input
                        value={colType.webhooks[x].url}
                        onChange={(e) => {
                          const webhooks = { ...colType.webhooks };
                          webhooks[x].url = e.target.value;
                          setColType((prev) => (!prev ? prev : { ...prev, webhooks }));
                        }}
                        placeholder="Type your url"
                        className="w-full placeholder-gray-300"
                      ></Input>
                    </div>
                  </div>
                ))}
              </>
            )}
          </div>
        </div>
      )}
    </div>
  );
};

export default Webhooks;
